@import url(https://fonts.googleapis.com/css2?family=Comfortaa&display=swap);
:root {
    --primary-color: #B35AA8;
    --secondary-color: #8b4483;
}
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Comfortaa', cursive;
}

#body {
    color: white;
    line-height: 1.6;
    overflow: hidden;
}

ul {
    list-style-type: none;
}

a {
    text-decoration: none;
    color: #333;
}

h1 {
    margin: 10px 0;
}

p {
    margin: 10px 0;
}

img {
    width: 100%;
}

.navbar {
    background: linear-gradient(to right, #ff81f0, #6d3a67);
    /*background-color: #B35AA8;*/
    color: #333;
    min-height: 10vh;
    z-index: 1;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    border-bottom: 1px solid #333;
}

.navbar ul {
    display: flex;
}

.navbar li a {
    padding: 10px;
    margin: 0 5px;
    color: white;
}

.navbar li a:hover {
    border-bottom: 1px solid white;
}

.container {
    max-width: 1100px;
    margin: 0 auto;
    overflow: auto;
    padding: 0 40px;
    
}

.flex {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
}

/* MAIN */

.main {
    background-color: #333;
    color: white;
    
}

.main .stuff {
  
  height: 100%;
  width: 100%;
  
}

.stuff div {
  padding: 10px;
}


.stuff div a {
  text-decoration: none;
  color: #333;
  width: 100%;
}


/* auth */
#auth-wrapper {
  display: flex;
  height: 100%;
  justify-content: center;
  align-items: center;
  
}

.auth {
  width: 633px;
  height: 80%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  box-shadow: 0 0.125px 0.25rem 0 rgba(255, 255, 255, 0.15);
  padding: 0;
  background-color: #B35AA8;
  background-color: var(--primary-color);
  border-radius: 10px;
}

.auth input {
    width: 80%;
    outline: none;
    border: none;
    border-radius: 3px;
    height: 20px;
}

.label1 {
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 1;
    grid-row-end: 2;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    color: white;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
}
  
.label2 {
    grid-column-start: 2;
    grid-column-end: 3;
    grid-row-start: 1;
    grid-row-end: 2;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    color: white;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
}
  
.label1:hover, .label2:hover {
    cursor: pointer;
}
  
.infoarea {
    grid-column-start: 1;
    grid-column-end: 3;
    grid-row-start: 2;
    grid-row-end: 16;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    text-align: center;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
}

.auth-selected {
    background-color: #333;
}

.infoarea button {
    width: 80%;
    height: 30px;
    background-color: #B35AA8;
    background-color: var(--primary-color);
    border: none;
    outline: none;
    border-radius: 5px;
    color: white;
    font-family: 'Comfortaa', cursive;
}



.infoarea button:hover {
    cursor: pointer;
    border: 1px solid black;
}

#show-wrapper {
    margin-top: 10vh;
    height: 90vh;
    overflow-y: scroll;
    overflow-x: hidden;
    -ms-scroll-snap-type: y mandatory;
        scroll-snap-type: y mandatory;
}

#show-wrapper div {
    animation: animation1;
    animation-duration: 2s;
    scroll-snap-align: start;
}

#home {    
    height: 90vh;
    position: relative;
    padding: 0;
}

#home svg {
    height: 50%;
    
    padding: 0px;
    position: absolute;
    bottom: 0;
    left: 0;
}

#home button {
    width: 500px;
    height: 30px;
    background-color: #B35AA8;
    background-color: var(--primary-color);
    border: none;
    outline: none;
    border-radius: 5px;
    color: white;
    font-family: 'Comfortaa', cursive;
}

#home button:hover {
    cursor: pointer;
    border: 1px #333 solid;
}

#section2 {
    height: 90vh;
    position: relative;
    padding: 0;
}

#downloads {
    margin: 0;
    padding: 0;
}

#section2 svg {
    height: 50%;
    
    padding: 0px;
    position: absolute;
    bottom: 0;
    right: 0;
}

#section2 #blackthing {
    height: 10vh;
    padding: 0px;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    background-color: #333;
}

.title-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    height: 45vh;
}

#showcase-title {
    margin: 0;
    text-align: center;
    font-weight: 200;
}
  
#section2 {
    background-color: #B35AA8;
    background-color: var(--primary-color);
    height: 90vh;
}

#login-register {
    background-color: #333;
    height: 90vh;
}

