/* listnav */
.listnav {
  height: 100%;
  width: 250px;
  background-color: rgb(48, 48, 48);
  box-shadow: 0 0.125px 0.25rem 0 rgba(0, 0, 0, 0.15);
  overflow-y: scroll;
}

.listnav h3 {
    padding: 10px;
}

.listnav hr {
    margin-bottom: 10px;
}

.listnav li {
  padding: 10px 15px;
  color: var(--secondary-color);
  border-radius: 10px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.listnav li div {
    width: 32px;
    height: 32px;
    border-radius: 10px;
    margin-right: 15px;
}

.listnav li p {
    height: 100%;
    margin: 0;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.listnav a:hover{
  border-bottom: none;
  
}

.listnav li:hover {
  background-color: rgb(61, 60, 60);
}

.new {
  width: 100%;
  line-height: 1.6;
  background-color: var(--secondary-color);
  border: none;
  outline: none;
  color: white;
  border-radius: 10px;
  padding: 5px;
  font-family: 'Comfortaa', cursive;
}

.new:hover {
  cursor: pointer;
  border: 1px solid black;
}

#buttonwrap:hover {
    background-color: rgb(48, 48, 48);
}


/* chat */

.chat {
    
    width: 100%;
    height: 90vh;
    display: flex;
    flex-direction: column;
    padding: 0px !important; 
   
}

.top {
    flex: 8% 1;
    background-color: #333;
    box-shadow: 0px 4px 2px -2px rgb(48, 48, 48);
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.top h2 {
    padding: 10px;
}

.chatarea {
    flex: 84% 1;
    overflow-y: scroll;
    display: flex;
    flex-direction: column-reverse;
    
}

.msg {
    display: flex;
    flex-direction: column;
}

.msg div:nth-child(1) {
    display: flex;
    align-items: baseline;

    justify-content: space-between;
    
}

.msg div {
    padding: 0 10px;
}


.msg div p {
    margin: 0 !important;
    font-size: .9em;
    color: white;
}

.msg b {
    color: var(--primary-color);
}

.msgtxt {
    font-size: 1em !important;
    color: white;
    word-wrap: normal;
}

.msg div span {
    color: gray;
    font-size: .8em;
}
.type {
    flex: 8% 1;
    box-shadow: 0px -4px 2px -2px rgb(48, 48, 48);
    
}

.type form input {
    height: 80%;
    width: 100%;
    background-color: rgb(48, 48, 48);
    border-radius: 10px;
    outline: none;
    border: rgb(14, 14, 14) 1px solid;
    color: white;
    padding: 10px;
}

.type form input:focus {
    border: 1px var(--primary-color) solid;
}

.new-input {
    height: 80%;
    width: 100%;
    background-color: rgb(48, 48, 48);
    border-radius: 10px;
    outline: none;
    border: rgb(14, 14, 14) 1px solid;
    color: white;
    padding: 10px;
}

.new-input:focus {
    border: 1px var(--primary-color) solid;
}



